.illustration-team {
  width: 24.25em;
  height: 17.3125em;
  border-radius: 0.625em;
  position: relative;
}

.illustration-team .hands {
  margin-top: 1.875em;
}

.illustration-team .hand-left {
  width: 9.9375em;
  height: 17.625em;
  animation: hand-left 4s cubic-bezier(0.55, 0.055, 0.675, 0.19) infinite;
}

.illustration-team .hand-right {
  width: 9.9375em;
  height: 17.625em;
  margin-left: 4.0625em;
  animation: hand-right 4s cubic-bezier(0.55, 0.055, 0.675, 0.19) infinite;
}

@keyframes hand-left {
  45%,
  0%,
  55%,
  100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(35px);
  }
}
@keyframes hand-right {
  45%,
  0%,
  55%,
  100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-35px);
  }
}
.illustration-team .marks {
  position: absolute;
  top: -1.875em;
  left: 50%;
  margin-left: -0.1875em;
}
.illustration-team .marks .mark {
  position: absolute;
  top: 0;
  width: 0.3125em;
  border-radius: 0.1875em;
  background: #4c4f5a;
}
.illustration-team .marks .mark:before,
.illustration-team .marks .mark:after {
  content: "";
  position: absolute;
  left: 0;
  width: 0.3125em;
  border-radius: 0.1875em;
  background: #4c4f5a;
}
.illustration-team .marks .mark:before {
  top: -1.875em;
  height: 1.25em;
}
.illustration-team .marks .mark:after {
  top: -3.125em;
  height: 0.625em;
}
.illustration-team .marks .mark-1,
.illustration-team .marks .mark-4,
.illustration-team .marks .mark-5 {
  height: 3.125em;
}
.illustration-team .marks .mark-2,
.illustration-team .marks .mark-3 {
  height: 1.875em;
}
.illustration-team .marks .mark-2 {
  transform: translate(30px, 10px) rotate(30deg);
}
.illustration-team .marks .mark-3 {
  transform: translate(-30px, 10px) rotate(-30deg);
}
.illustration-team .marks .mark-4 {
  transform: translate(-45px, 25px) rotate(-60deg);
}
.illustration-team .marks .mark-5 {
  transform: translate(45px, 25px) rotate(60deg);
}
.illustration-team .marks .mark {
  animation: mark-color 4s linear 0.5s infinite;
}
.illustration-team .marks .mark:before {
  animation: mark-color 4s linear 1.5s infinite;
}
.illustration-team .marks .mark:after {
  animation: mark-color 4s linear 2.5s infinite;
}

@keyframes mark-color {
  40%,
  60% {
    background: #4c4f5a;
  }
  41%,
  59% {
    background: #fff;
  }
}

